import React from 'react';

import onlineIcon from '../../icons/online.png';

import './TextContainer.css';

const TextContainer = ({ users }) => (
  <div className="textContainer">
    <div>
      <h1>实时在线聊天程序demo <span role="img" aria-label="emoji"></span></h1>
      <h2>使用 React, Express, Node and Socket.IO 搭建<span role="img" aria-label="emoji"></span></h2>
      <h2>试试看吧！ <span role="img" aria-label="emoji"></span></h2>
    </div>
    {
      users
        ? (
          <div>
            <h1> 当前在线 :</h1>
            <div className="activeContainer">
              <h2>
                {users.map(({name}) => (
                  <div key={name} className="activeItem">
                    <img className="status" alt="Online Icon" src={onlineIcon}/>
                    {name}
                  </div>
                ))}
              </h2>
            </div>
          </div>
        )
        : null
    }
  </div>
);

export default TextContainer;